import React, { Component } from 'react';
import '../TodoApp/TodoApp.js'
import './TodoApp.css'
class TodoApp extends Component {
    state = {
        input: "",
        items: []
    }
    handleChange = (event) => {
        this.setState({
            input: event.target.value
        });
    };
    storeItems = event => {
        event.preventDefault();
        const { input } = this.state;

        this.setState({
            items: [...this.state.items, input],
            input: ""
        });
    }
    onDelete = (key) => {
        this.setState({
            items: this.state.items.filter((data, index) => (
                index !== key
            ))
        })
    }
    onEdit = (key) => {
        const toEditItem = this.state.items.filter((data, index) => (
            index === key
        ));
        const toEdit = prompt("edit value", toEditItem);
        const edited = this.state.items.filter((data, index) => (
            index !== key
        ));
        edited.splice(key, 0, toEdit);
        this.setState({
            items: edited
        })
    }
    render() {
        const { input, items } = this.state;

        return (
            <div className="todo-container">
                <form action="" onSubmit={this.storeItems}>
                    <h1 className="todo-header">Todo App</h1>
                    <div className="input-section">
                        <input value={input} onChange={this.handleChange} type="text" placeholder="Please Enter To Do Text" />
                    </div>
                </form>
                <ul>
                    {items.map((data, index) => {
                        return (
                            <li key={index}>{data}
                                <span>
                                    <i className="edit-icon fa fa-edit" onClick={() => this.onEdit(index)}></i>
                                    <i className="fas fa-trash-alt" onClick={() => this.onDelete(index)}></i>
                                </span></li>
                        );
                    })}
                </ul>
            </div>
        );
    }
}

export default TodoApp;